/* eslint-disable no-param-reassign */
import m from 'mithril';
import input from './components/input';
import config from '../saas/config';

// eslint-disable-next-line no-undef
const HOST = config[window.env].apiUrl;

const activationComponent = {
  async oninit({ state, attrs }) {
    Object.assign(state, {
      error: null,
      success: false,
      showPass: false,
      mail: '',
      pass: '',
      userExist: false,
    });

    // check if user exists
    const user = await activationComponent.checkForUser({ invitationId: attrs.invitationId });

    if (user) {
      state.userExist = true;
      m.redraw();

      // send request to accept the invitation
      await activationComponent.acceptsInvitation({
        invitationId: attrs.invitationId,
        password: state.password,
        passConf: state.passConf,
      });

      // show an invitation accepted then move on to login. otherwise user doesnt know what just happend
      m.route.set('/');
    } else {

    }
  },
  async acceptsInvitation({ invitationId, password, passConf }) {
    return m.request({
      url: `${HOST}/saasAuth/activateInvitation/${invitationId}`,
      method: 'POST',
      data: {
        password,
      },
    });
  },
  async checkForUser({ invitationId }) {
    try {
      await m.request({
        url: `${HOST}/saasAuth/checkUser`,
        method: 'POST',
        data: {
          invitationId,
        },
      });

      return true;
    } catch (_) {
      return false;
    }
  },
  oncreate({ state, attrs }) {
    // Fetch all the forms we want to apply custom Bootstrap validation styles to
    // eslint-disable-next-line no-undef
    const forms = document.getElementsByClassName('needs-validation');
    // Loop over them and prevent submission
    Array.prototype.filter.call(forms, (form) => {
      form.addEventListener('submit', async (event) => {
        event.preventDefault();
        if (form.checkValidity() === false) {
          event.stopPropagation();
          return form.classList.add('was-validated');
        }

        try {
          const {
            password, passConf,
          } = state;

          if (password !== passConf) {
            state.error = 'Your Password and Confirmation are not the same, Please confirm this and retry';
            m.redraw();
            return null;
          }

          state.loading = true;
          m.redraw();

          // talk to server
          await activationComponent.acceptsInvitation({
            invitationId: attrs.invitationId,
            password: state.password,
            passConf: state.passConf,
          });
          state.success = true;
          state.error = undefined;
          state.loading = false;
          m.redraw();

          setTimeout(() => {
            m.route.set('/login');
          }, 2000);
        } catch (err) {
          state.error = err.message;
          state.loading = false;
          m.redraw();
        }
      }, false);
    });
  },
  view({ state }) {
    const { success, error } = state;
    return m('div.container.h-100vh.d-flex.align-items-center',
      m('div.col',
        m('div.row.justify-content-center',
          m('div.col-md-6.col-lg-5.col-xl-4',
            m('div',
              [
                m('div.mb-5.text-center',
                  [
                    m('p',
                      'Activate your account'),
                    m("img[src='/assets/media/logos/all-black.png'][alt='']",{style:{width: '220px'}}),
                  ]),
                m('span.clearfix'),
                !error
                  ? null
                  : [
                    m('br'),
                    m(".alert.alert-danger.text-center[role='alert']",
                      [
                        m('.alert-icon',
                          m('i.flaticon-questions-circular-button')),
                        m('.alert-text', error),
                      ]),
                  ],

                !success
                  ? null
                  : [
                    m('br'),
                    m(".alert.alert-info.text-center[role='alert']",
                      [
                        m('.alert-text',
                          'Your account has been successfully activated, please login using the credentials you just set.'),
                      ]),
                  ],

                state.userExist
                  ? 'Please wait a few minutes while your access is granted'
                  : m('form', {
                    novalidate: true,
                    class: 'needs-validation',
                  },
                  [
                    m(input, {
                      prepend: [
                        m('div.input-group-prepend',
                          m('span.input-group-text',
                            m('i.fas.fa-key'))),
                      ],
                      value: state.mail,
                      oninput: m.withAttr('value', (v) => { state.password = v; }),
                      error: 'Please enter a password',
                      type: !state.showPass ? 'password' : 'text',
                      placeholder: 'Password',
                      label: 'Set your New Password',
                      id: 'input-password',
                      append: [
                        m('div.input-group-append', {
                          style: {
                            cursor: 'pointer',
                          },
                          onclick() {
                            // eslint-disable-next-line no-param-reassign
                            state.showPass = !state.showPass;
                            setTimeout(() => {
                              // eslint-disable-next-line no-param-reassign
                              state.showPass = false;
                              m.redraw();
                            }, 2000);
                            m.redraw();
                          },
                        },
                        m('span.input-group-text',
                          m('i.fas.fa-eye'))),
                      ],
                    }),

                    m(input, {
                      prepend: [
                        m('div.input-group-prepend',
                          m('span.input-group-text',
                            m('i.fas.fa-key'))),
                      ],
                      value: state.mail,
                      oninput: m.withAttr('value', (v) => { state.passConf = v; }),
                      error: 'Please enter a password',
                      type: 'password',
                      placeholder: 'Password',
                      label: 'Confirm that Password',
                      id: 'input-password',
                    }),

                    m('div.mt-4',
                      m("button.btn.btn-block.btn-primary[type='submit']",
                        'Activate my account')),
                  ]),
                m('div.mt-4.text-center',
                  [
                    m('small',
                      'Also try'),
                    m("a.small.font-weight-bold[href='/login']", {
                      oncreate: m.route.link,
                      onupdate: m.route.link,
                    },
                    ' Log In'),
                    ' or',
                    m("a.small.font-weight-bold[href='/register']", {
                      oncreate: m.route.link,
                      onupdate: m.route.link,
                    },
                    ' Create account'),
                  ]),
              ])))));
  },
};


export default activationComponent;
