import m from 'mithril';

export default {
  view({ attrs }) {
    const {
      label, type = 'text', id, placeholder, error, classes, required = true, icon, prepend, append,
      oninput, onchange,
    } = attrs;
    return [
      label ? m('label.form-control-label', { for: id }, label) : null,
      m('div.input-group.input-group-merge',
        [
          prepend,
          m('input.form-control', {
            class: classes,
            'data-error': error,
            required,
            type,
            id,
            placeholder,
            oninput,
            onchange,
          }),
          append,
        ]),
      m('div.invalid-feedback', error),
      m('div.valid-feedback', 'looks ok'),
    ];
  },
};
