/* eslint-disable no-param-reassign */
import m from 'mithril';
import input from './components/input';
import config from '../saas/config';

// eslint-disable-next-line no-undef
const HOST = config[window.env].apiUrl;

const loginComponent = {
  oninit({ state }) {
    Object.assign(state, {
      error: null,
      success: false,
      showPass: false,
      mail: '',
      pass: '',
      loggedIn:false
    });

    // eslint-disable-next-line no-undef
    const token = localStorage.getItem('token');

    if (token) {
      state.loggedIn = true
      m.redraw()
    }
  },
  async login({ email, password }) {
    return m.request({
      url: `${HOST}/saasAuth/login`,
      method: 'POST',
      data: {
        email,
        password,
      },
    });
  },
  oncreate({ state }) {
    // Fetch all the forms we want to apply custom Bootstrap validation styles to
    // eslint-disable-next-line no-undef
    const forms = document.getElementsByClassName('needs-validation');
    // Loop over them and prevent submission
    Array.prototype.filter.call(forms, (form) => {
      form.addEventListener('submit', async (event) => {
        event.preventDefault();
        if (form.checkValidity() === false) {
          event.stopPropagation();
          return form.classList.add('was-validated');
        }

        try {
          state.loading = true;
          m.redraw();

          // talk to server
          const { token } = await loginComponent.login({
            email: state.mail,
            password: state.pass,
          });
          // eslint-disable-next-line no-undef
          localStorage.setItem('token', token);
          state.success = true;
          state.error = undefined;
          state.loading = false;
          m.redraw();

          // redirect to dashboards
          // setTimeout(() => {
          // eslint-disable-next-line no-undef
          window.location.replace('/');
          // }, 1000);
        } catch (err) {
          state.error = err.message;
          state.loading = false;
          m.redraw();
        }
      }, false);
    });
  },
  view({ state }) {
    const { success, error } = state;
    return m('div.container.h-100vh.d-flex.align-items-center',
      m('div.col',
        m('div.row.justify-content-center',
          m('div.col-md-6.col-lg-5.col-xl-4',
            m('div',
              [
                m('div.mb-5.text-center',
                  [
                    m('p',
                      'Login'),
                    m("img[src='./assets/media/logos/all-black.png'][alt='']",{style:{width: '220px'}}),
                  ]),
                m('span.clearfix'),
                !error
                  ? null
                  : [
                    m('br'),
                    m(".alert.alert-danger[role='alert']",
                      [
                        m('.alert-icon',
                          m('i.flaticon-questions-circular-button')),
                        m('.alert-text', error),
                      ]),
                  ],

                !success
                  ? null
                  : [
                    m('br'),
                    m(".alert.alert-info[role='alert']",
                      [
                        m('.alert-text',
                          'Successfully logged in, redirecting...'),
                      ]),
                  ],
                m('form', {
                  novalidate: true,
                  class: 'needs-validation',
                },
                state.loggedIn ? [
                  m('div.mt-4.text-center',
                  [
                    m('p','You are already logged in, '),
                    m("a.small.font-weight-bold[href='#']", {
                      onclick(){
                        localStorage.clear()
                        state.loggedIn = false
                        m.redraw()
                      },
                    },
                    ' logout? '),
                    "or",
                    m("a.small.font-weight-bold[href='/']",
                    ' proceed to your dashbaord?'),
                  ])
                ]:[
                  [
                  m('div.form-group', m(input, {
                    prepend: [
                      m('div.input-group-prepend',
                        m('span.input-group-text',
                          m('i.fas.fa-user'))),
                    ],
                    value: state.mail,
                    oninput: m.withAttr('value', (v) => { state.mail = v; }),
                    label: 'Email address',
                    error: 'Please enter a valid email',
                    type: 'email',
                    placeholder: 'ie. username@domain.com',
                    id: 'input-mail',
                  })),
                  m('div.form-group.mb-4',
                    [
                      m('div.d-flex.align-items-center.justify-content-between',
                        [
                          m('div',
                            m('label.form-control-label',
                              'Password')),
                          m('div.mb-2',
                            m("a.small.text-muted.text-underline--dashed.border-primary[href='/recover/send']", {
                              oncreate: m.route.link,
                              onupdate: m.route.link,
                            },
                            'Lost password?')),
                        ]),
                      m('div.input-group.input-group-merge',
                        [
                          m(input, {
                            prepend: [
                              m('div.input-group-prepend',
                                m('span.input-group-text',
                                  m('i.fas.fa-key'))),
                            ],
                            value: state.mail,
                            oninput: m.withAttr('value', (v) => { state.pass = v; }),
                            error: 'Please enter a password',
                            type: !state.showPass ? 'password' : 'text',
                            placeholder: 'Password',
                            id: 'input-password',
                            append: [
                              m('div.input-group-append', {
                                style: {
                                  cursor: 'pointer',
                                },
                                onclick() {
                                  // eslint-disable-next-line no-param-reassign
                                  state.showPass = !state.showPass;
                                  setTimeout(() => {
                                    // eslint-disable-next-line no-param-reassign
                                    state.showPass = false;
                                    m.redraw();
                                  }, 2000);
                                  m.redraw();
                                },
                              },
                              m('span.input-group-text',
                                m('i.fas.fa-eye'))),
                            ],
                          }),
                        ]),
                    ]),
                  m('div.mt-4',
                    m("button.btn.btn-block.btn-primary[type='submit']",
                      ' Sign in')),
                
                m('div.mt-4.text-center',
                  [
                    m('small',
                      'Not registered?'),
                    m("a.small.font-weight-bold[href='/register']", {
                      oncreate: m.route.link,
                      onupdate: m.route.link,
                    },
                    ' Create account'),
                  ])]
                ]
              )])))))
  },
};


export default loginComponent;
