/* eslint-disable no-param-reassign */
import m from 'mithril';
import input from './components/input';
import config from '../saas/config';

// eslint-disable-next-line no-undef
const HOST = config[window.env].apiUrl;

const recoveryComponent = {
  oninit({ state }) {
    Object.assign(state, {
      error: null,
      success: false,
      showPass: false,
      mail: '',
      pass: '',
    });
  },
  async recover({ id, password, confirmPassword: confirm }) {
    return m.request({
      url: `${HOST}/saasAuth/resetPassword/${id}`,
      method: 'POST',
      data: {
        password,
        confirm,
      },
    });
  },
  oncreate({ state, attrs }) {
    // Fetch all the forms we want to apply custom Bootstrap validation styles to
    // eslint-disable-next-line no-undef
    const forms = document.getElementsByClassName('needs-validation');
    // Loop over them and prevent submission
    Array.prototype.filter.call(forms, (form) => {
      form.addEventListener('submit', async (event) => {
        event.preventDefault();
        if (form.checkValidity() === false) {
          event.stopPropagation();
          return form.classList.add('was-validated');
        }

        try {
          const {
            pass, passConf,
          } = state;

          if (pass !== passConf) {
            state.error = 'Your Password and Confirmation are not the same, Please confirm this and retry';
            m.redraw();
            return null;
          }

          state.loading = true;
          m.redraw();

          // talk to server
          await recoveryComponent.recover({
            id: attrs.recoveryId,
            password: state.pass,
            confirmPassword: state.passConf,
          });

          state.success = true;
          state.error = undefined;
          state.loading = false;
          m.redraw();

          // redirect to dashboards
          setTimeout(() => {
          // eslint-disable-next-line no-undef
            m.route.set('/login');
          }, 2000);
        } catch (err) {
          state.error = err.message;
          state.loading = false;
          m.redraw();
        }
      }, false);
    });
  },
  view({ state }) {
    const { success, error } = state;
    return m('div.container.h-100vh.d-flex.align-items-center',
      m('div.col',
        m('div.row.justify-content-center',
          m('div.col-md-6.col-lg-5.col-xl-4',
            m('div',
              [
                m('div.mb-5.text-center',
                  [
                    m('p',
                      'Password reset'),
                    m("img[src='/assets/media/logos/all-black.png'][alt='']",{style:{width: '220px'}}),
                  ]),
                m('span.clearfix'),
                !error
                  ? null
                  : [
                    m('br'),
                    m(".alert.alert-danger[role='alert']",
                      [
                        m('.alert-icon',
                          m('i.flaticon-questions-circular-button')),
                        m('.alert-text', error),
                      ]),
                  ],

                !success
                  ? null
                  : [
                    m('br'),
                    m(".alert.text-center.alert-info[role='alert']",
                      [
                        m('.alert-text',
                          'Successfully reset your password, sending you to login..'),
                      ]),
                  ],
                m('form', {
                  novalidate: true,
                  class: 'needs-validation',
                },
                [
                  m(input, {
                    prepend: [
                      m('div.input-group-prepend',
                        m('span.input-group-text',
                          m('i.fas.fa-key'))),
                    ],
                    value: state.mail,
                    oninput: m.withAttr('value', (v) => { state.pass = v; }),
                    error: 'Please enter a password',
                    type: !state.showPass ? 'password' : 'text',
                    placeholder: 'Password',
                    label: 'New Password',
                    id: 'input-password',
                    append: [
                      m('div.input-group-append', {
                        style: {
                          cursor: 'pointer',
                        },
                        onclick() {
                          // eslint-disable-next-line no-param-reassign
                          state.showPass = !state.showPass;
                          setTimeout(() => {
                            // eslint-disable-next-line no-param-reassign
                            state.showPass = false;
                            m.redraw();
                          }, 2000);
                          m.redraw();
                        },
                      },
                      m('span.input-group-text',
                        m('i.fas.fa-eye'))),
                    ],
                  }),
                  m('div.form-group', m(input, {
                    prepend: [
                      m('div.input-group-prepend',
                        m('span.input-group-text',
                          m('i.fas.fa-key'))),
                    ],
                    value: state.passConf,
                    oninput: m.withAttr('value', (v) => { state.passConf = v; }),
                    label: 'Enter New Password again for confirmation',
                    type: 'password',
                    placeholder: '********',
                    id: 'input-mail',
                  })),
                  m('div.mt-4',
                    m("button.btn.btn-block.btn-primary[type='submit']",
                      ' Reset my password')),
                ]),
                m('div.mt-4.text-center',
                  [
                    m('small',
                      'Dont wish to do this? You can'),
                    m("a.small.font-weight-bold[href='/login']", {
                      oncreate: m.route.link,
                      onupdate: m.route.link,
                    },
                    ' Login here'), ' instead',
                  ]),

              ])))));
  },
};


export default recoveryComponent;
