/* eslint-disable no-param-reassign */
import m from 'mithril';
import input from './components/input';

const registrationComponent = {
  async register({
    email, password, firstName, contact, orgName,
  }) {
    return m.request({
      url: '/registration',
      method: 'POST',
      data: {
        firstName,
        contact,
        email,
        password,
        orgName,
      },
    });
  },
  oninit({ state }) {
    Object.assign(state, {
      error: null,
      success: false,
      email: '',
      firstName: '',
      contact: '',
      password: '',
    });
  },
  oncreate({ state }) {
    // eslint-disable-next-line no-undef
    const forms = document.getElementsByClassName('needs-validation');
    // Loop over them and prevent submission
    Array.prototype.filter.call(forms, (form) => {
      form.addEventListener('submit', async (event) => {
        event.preventDefault();
        if (form.checkValidity() === false) {
          event.stopPropagation();
          return form.classList.add('was-validated');
        }

        try {
          const {
            mail: email, firstName, phone: contact, password, org: orgName, confPass,
          } = state;

          if (password !== confPass) {
            state.error = 'password and confirmation are not equal';
            m.redraw();
            return null;
          }

          state.loading = true;
          m.redraw();

          // talk to server
          const { token } = await registrationComponent.register({
            firstName,
            contact,
            email,
            password,
            orgName,
          });
          // eslint-disable-next-line no-undef
          localStorage.setItem('token', token);
          state.success = true;
          state.error = undefined;
          state.loading = false;
          m.redraw();

          // redirect to dashboards
          setTimeout(() => {
            // eslint-disable-next-line no-undef
            window.location.replace('/');
          }, 1000);
        } catch (err) {
          state.error = err.message;
          state.loading = false;
          m.redraw();
        }
      });
    });
  },
  view({ state }) {
    const {
      error, success,
    } = state;
    return m('div.container.min-vh-100.d-flex.align-items-center',
      m('div.col.py-5',
        m('div.row.justify-content-center',
          m('div.col-md-8.col-lg-6',
            m('div',
              [
                m('div.mb-5.text-center',
                  [
                    m('p',
                      'Create account'),
                    m("img[src='./assets/media/logos/all-black.png'][alt='']",{style:{width: '220px'}}),
                  ]),
                m('span.clearfix'),
                m('form.needs-validation', {
                  novalidate: true,
                },
                [
                  m('div.form-group',
                    [
                      m(input, {
                        prepend: [
                          m('div.input-group-prepend',
                            m('span.input-group-text',
                              m('i.fas.fa-user'))),
                        ],
                        value: state.firstName,
                        oninput: m.withAttr('value', (v) => { state.firstName = v; }),
                        label: 'Your name',
                        error: 'What name should we use to call you?',
                        type: 'text',
                        placeholder: 'ie. James',
                        id: 'input-name',
                      }),
                    ]),
                  m('div.form-group',
                    [
                      m(input, {
                        prepend: [
                          m('div.input-group-prepend',
                            m('span.input-group-text',
                              m('i.fas.fa-building'))),
                        ],
                        value: state.org,
                        oninput: m.withAttr('value', (v) => { state.org = v; }),
                        label: 'Organisation name',
                        error: 'Please enter an org name',
                        type: 'text',
                        placeholder: 'ie. Amazing Company',
                        id: 'input-org',
                      }),
                    ]),
                  m('div.form-group',
                    [
                      m(input, {
                        prepend: [
                          m('div.input-group-prepend',
                            m('span.input-group-text',
                              m('i.fas.fa-at'))),
                        ],
                        value: state.mail,
                        oninput: m.withAttr('value', (v) => { state.mail = v; }),
                        label: 'Email address',
                        error: 'Please enter an email we can reach you with',
                        type: 'text',
                        placeholder: 'ie. username@mail.com',
                        id: 'input-mail',
                      }),
                    ]),
                  m('div.form-group',
                    [
                      m(input, {
                        prepend: [
                          m('div.input-group-prepend',
                            m('span.input-group-text',
                              m('i.fas.fa-mobile'))),
                        ],
                        value: state.phone,
                        oninput: m.withAttr('value', (v) => { state.phone = v; }),
                        label: 'Phone Number',
                        error: 'Please enter an phone number name',
                        type: 'text',
                        placeholder: 'ie. +1 5 123 657',
                        id: 'input-num',
                      }),
                    ]),
                  m('div.form-group.mb-4',
                    [
                      m(input, {
                        prepend: [
                          m('div.input-group-prepend',
                            m('span.input-group-text',
                              m('i.fas.fa-key'))),
                        ],
                        value: state.password,
                        oninput: m.withAttr('value', (v) => { state.password = v; }),
                        label: 'Password',
                        error: 'Please enter a password',
                        type: !state.showPass ? 'password' : 'text',
                        placeholder: '********',
                        id: 'input-pass',
                        append: [
                          m('div.input-group-append', {
                            style: {
                              cursor: 'pointer',
                            },
                            onclick() {
                              // eslint-disable-next-line no-param-reassign
                              state.showPass = !state.showPass;
                              setTimeout(() => {
                                // eslint-disable-next-line no-param-reassign
                                state.showPass = false;
                                m.redraw();
                              }, 2000);
                              m.redraw();
                            },
                          },
                          m('span.input-group-text',
                            m('i.fas.fa-eye'))),
                        ],
                      }),
                    ]),
                  m('div.form-group',
                    [
                      m(input, {
                        prepend: [
                          m('div.input-group-prepend',
                            m('span.input-group-text',
                              m('i.fas.fa-key'))),
                        ],
                        value: state.confPass,
                        oninput: m.withAttr('value', (v) => { state.confPass = v; }),
                        label: 'Confirm password',
                        error: 'Please enter a confirmation password',
                        type: 'password',
                        placeholder: '********',
                        id: 'input-confirmation-pass',
                      }),
                    ]),
                  m('div.my-4',
                    [
                      m('div.custom-control.custom-checkbox.mb-3',
                        [
                          m("input.custom-control-input[required=true][type='checkbox'][id='check-terms']"),
                          m("label.custom-control-label[for='check-terms']",
                            [
                              'I agree to the',
                              m("a[href='#']",
                                ' terms and conditions'),
                            ]),
                        ]),
                      m('div.custom-control.custom-checkbox',
                        [
                          m("input.custom-control-input[required=true][type='checkbox'][id='check-privacy']"),
                          m("label.custom-control-label[for='check-privacy']",
                            [
                              'I agree to the',
                              m("a[href='#']",
                                ' privacy policy'),
                            ]),
                        ]),
                    ]),
                  !error
                    ? null
                    : [
                      m('br'),
                      m(".alert.alert-danger[role='alert']",
                        [
                          m('.alert-icon',
                            m('i.flaticon-questions-circular-button')),
                          m('.alert-text', error),
                        ]),
                    ],

                  !success
                    ? null
                    : [
                      m('br'),
                      m(".alert.alert-info.text-center[role='alert']",
                        [
                          m('.alert-text',
                            state.resetting === true
                              ? 'We just sent you an email, please use it follow the instructions in there'
                              : 'Successfully registered, just one sec...'),
                        ]),
                    ],
                  m('div.mt-4',
                    m("button.btn.btn-block.btn-primary[type='submit']",
                      'Create my account')),
                ]),
                m('div.mt-4.text-center',
                  [
                    m('small',
                      'Already have an acocunt?'),
                    m("a.small.font-weight-bold[href='/login']", {
                      oncreate: m.route.link,
                      onupdate: m.route.link,
                    },
                    ' Sign in'),
                  ]),
              ])))));
  },
};


export default registrationComponent;
