import 'babel-polyfill';
import m from 'mithril';

import login from './login';
import register from './register';
import otpSms from './sms-otp';
import otpMail from './sms-mail';
import sendRecover from './send-recover';
import recover from './recover';
import invitations from './invitations';

m.route.prefix('');
// eslint-disable-next-line no-undef
m.route(document.getElementById('auth'), '/login', {
  '/login': login,
  '/register': register,
  '/sms-otp': otpSms,
  '/mail-otp': otpMail,
  '/recover/send': sendRecover,
  '/recover/:recoveryId': recover,
  '/invitation/:invitationId': invitations,
});
